import React from 'react';
import NavLink from './NavLink';
import MAP from './componentInfo.tpl';
const { pathname } = window.location;

// 构造导航
function NavLinks(k, i) {
  let href = `components/${k}/demo/index.html`;

  if (pathname.indexOf('/imui') > -1) {
    href = `/imui/${href}`;
  }

  return (
    <li key={i}>
      <NavLink to={`/components/${k}`}>{MAP[k].name}</NavLink>
      <a title="新窗口" className="blank" target="_blank" href={href}>↗</a>
    </li>
  );
}

export default function Home(props) {
  return (
    <div>
      <aside>
        <h1>IMWEB · UI</h1>
        <ul>
          {Object.keys(MAP).map(NavLinks)}
        </ul>
      </aside>
      <section className="main">{props.children}</section>
    </div>
  );
}
